body {
  font-family: 'Lato', sans-serif;
  color: #212529;
  position: relative;
}

/* Rules for sizing the icon. */
.material-icons,
.material-symbols-outlined {
  vertical-align: middle;
  font-size: 24px;
}

.line-height-19 {
  line-height: 19px;
}

.object-fit-cover {
  object-fit: cover;
}

#sideNav {
  width: 300px;
  height: 100vh;
}

@media (max-width: 991px) {
  #sideNav {
    width: 100vw;
    height: unset;
  }
}

#sideNav .navbar-toggler-icon {
  color: #212529 !important;
}

#sideNav .nav-link:hover {
  color: #adb5bd;
}

#sideNav .nav-link.active {
  color: #ffc107;
}

.hero {
  background: url(../images/iriswang.jpg) no-repeat;
  background-position: center;
  background-size: cover;
  height: 70vh;
  width: 100%;
}

#about .personal-photo {
  width: 1rem;
}

#resume .resume-item {
  position: relative;
  padding-left: 20px;
}

#resume .resume-item::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50px;
  left: -10.5px;
  top: 0;
  background: white;
  border: 2px solid #ced4da;
}

.badge {
  font-family: 'Roboto Mono', monospace;
}
